import React, { Component } from 'react';


class Form extends Component {
    constructor(props) {
        super(props);

        this.state = {
            fullname: "Name",
            email: "E-mail",
            phone: "0000000000",
            feedback: "Write your Feedback",
            output: []

        }
    }
    func = () => {

        return (this.state.output[0] = this.state.fullname,
            this.state.output[1] = this.state.email
            , this.state.output[2] = this.state.phone
            , this.state.output[3] = this.state.feedback);

        }



    handlechangeall = (event) => {
        this.setState({ [event.target.name]: event.target.value })
    }


    rst = () => {
        this.setState({
            fullname: "Name",
            email: "E-mail",
            phone: "0000000000",
            feedback: "Write your Feedback",

        })

    }

    handlesubmit = (event) => {
        if (this.state.fullname === ("Name"))
            alert('Enter Name before Submit');
        else if (this.state.email === ("E-mail"))
            alert('Enter Email before Submit');
        else if (this.state.phone === ("0000000000"))
            alert('Enter PhoneNo before Submit');

        else if (this.state.feedback === ("Write your Feedback"))
            alert('Enter Feedback before Submit');

        else {
            alert(`My name is ${this.state.fullname},
      My email id is ${this.state.email},
      My mobile number is ${this.state.phone},
      My feedback to your company is ${this.state.feedback}
      `)
            this.setState({
                fullname: "Name",
                email: "E-mail",
                phone: "0000000000",
                feedback: "Write your Feedback",

            })
        }
        event.preventDefault();

    }

    render() {
        return (
            <>
                <div className='Left'>
                    <form onSubmit={this.handlesubmit} >

                        <label> fullname </label><br />
                        <input type="text" name="fullname" value={this.state.fullname}
                            onChange={this.handlechangeall} /> <br />

                        <label> Email </label><br />
                        <input type="email" name="email" value={this.state.email}
                            onChange={this.handlechangeall} /> <br />

                        <label> Mobile </label><br />
                        <input type="number" name="phone" value={this.state.phone}
                            onChange={this.handlechangeall} /> <br />

                        <label> Feedback </label><br />
                        <textarea value={this.state.feedback} name="feedback"
                            onChange={this.handlechangeall} />   <br />

                        <button onClick={this.func}>OKAY</button>
                        
                        <button onClick={this.rst}>RESET</button>
                        <br /> 
                        
                  
                        <input type="submit" value="Submit" />
                    </form>
                </div>
                <div className="Right">

                    <textarea className='tex2' value={this.state.output} name="output" /><br /><br />



                </div>

            </>)
    }

}

export default Form;